/** object scss **/
/* ===== 基础样式 ===== */
body {
  line-height: 1.5;
  font-family: "Roboto", "Helvetica Neue", "Helvetica", "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "\9ED1\4F53", Arial, sans-serif;
  font-size: 14px;
  padding: 0;
  margin: 0;
  color: #202121;
  background-color: #fff;
  text-align: center; }

a {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

button,
input,
select,
textarea {
  font-family: "Roboto", "Helvetica Neue", "Hiragino Sans GB", "Microsoft YaHei", "\9ED1\4F53", Arial, sans-serif; }

input,
select,
textarea {
  font-size: 14px; }

textarea,
input {
  color: #000; }

textarea {
  resize: none;
  overflow-y: auto; }

body {
  width: 100vw;
  overflow-x: hidden; }

.app {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column; }
  .app header {
    flex: 1; }
  .app main {
    flex: 12;
    padding: 0 14px; }

.pre-javascript {
  border-radius: 4px; }

.component-title {
  margin: 16px 0;
  font-size: 40px;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  line-height: 1.04;
  letter-spacing: 0em; }

.layout-header-nav {
  padding: 8px 14px;
  display: flex;
  justify-content: center; }
  .layout-header-nav a {
    color: #fff;
    width: 100%; }
    .layout-header-nav a button {
      width: 100%; }

.nav-button {
  border-radius: 4px; }

.nav-box {
  box-sizing: border-box;
  width: calc(100vw - 28px);
  border-radius: 4px;
  padding: 12px;
  background-color: #1d1d1d; }
  .nav-box-text {
    font-size: 18px;
    margin: 0;
    font-weight: bold;
    line-height: 1.8; }
  .nav-box-subText {
    font-size: 12px;
    color: #bdc1c0; }

.footer-nav {
  padding: 6px 0;
  color: #fff; }
  .footer-nav .nav-box-subText {
    color: #808080; }
  .footer-nav .nav-box {
    padding: 8px; }

.nav-box.white .nav-box-text {
  color: #fff; }

.nav-box.yellow .nav-box-text {
  color: #ffc400; }

.nav-box.green .nav-box-text {
  color: #00da6d; }

.nav-box.blue .nav-box-text {
  color: #558dff; }

/** pages scss **/
.api-box {
  display: flex;
  height: 100%;
  padding: 6px 0;
  box-sizing: border-box; }

.api-menu {
  min-width: 200px;
  width: 30%;
  max-width: 240px;
  height: 100%;
  border-right: 1px solid #ccc; }

.api-view {
  width: 100%;
  display: flex; }

.api-context {
  width: 100%; }
  .api-context-box {
    padding: 24px 0; }

.app-progress {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #fff; }
  .app-progress-bar {
    width: 200px;
    height: 4px;
    background: #3f51b5;
    animation-duration: 2s;
    animation-timing-function: cubic-bezier(0.65, 0.815, 0.735, 0.395);
    animation-iteration-count: infinite; }

@media (max-width: 2560px) {
  .app-progress-bar {
    animation-name: progress-transform-2560; } }

@media (max-width: 1920px) {
  .app-progress-bar {
    animation-name: progress-transform-1920; } }

@media (max-width: 1440px) {
  .app-progress-bar {
    animation-name: progress-transform-1440; } }

@media (max-width: 1200px) {
  .app-progress-bar {
    animation-name: progress-transform-1200; } }

@media (max-width: 800px) {
  .app-progress-bar {
    animation-name: progress-transform-800; } }

@media (max-width: 475px) {
  .app-progress-bar {
    animation-name: progress-transform-475; } }

@media (max-width: 375px) {
  .app-progress-bar {
    animation-name: progress-transform-375; } }

@keyframes progress-transform-2560 {
  from {
    transform: translate3d(0px, 0px, 0px); }
  75% {
    transform: translate3d(2760px, 0px, 0px); }
  to {
    transform: translate3d(2760px, 0px, 0px); } }

@keyframes progress-transform-1920 {
  from {
    transform: translate3d(0px, 0px, 0px); }
  75% {
    transform: translate3d(2120px, 0px, 0px); }
  to {
    transform: translate3d(2120px, 0px, 0px); } }

@keyframes progress-transform-1440 {
  from {
    transform: translate3d(0px, 0px, 0px); }
  75% {
    transform: translate3d(1640px, 0px, 0px); }
  to {
    transform: translate3d(1640px, 0px, 0px); } }

@keyframes progress-transform-1200 {
  from {
    transform: translate3d(0px, 0px, 0px); }
  75% {
    transform: translate3d(1400px, 0px, 0px); }
  to {
    transform: translate3d(1400px, 0px, 0px); } }

@keyframes progress-transform-800 {
  from {
    transform: translate3d(0px, 0px, 0px); }
  75% {
    transform: translate3d(1000px, 0px, 0px); }
  to {
    transform: translate3d(1000px, 0px, 0px); } }

@keyframes progress-transform-475 {
  from {
    transform: translate3d(0px, 0px, 0px); }
  75% {
    transform: translate3d(675px, 0px, 0px); }
  to {
    transform: translate3d(675px, 0px, 0px); } }

@keyframes progress-transform-375 {
  from {
    transform: translate3d(0px, 0px, 0px); }
  75% {
    transform: translate3d(575px, 0px, 0px); }
  to {
    transform: translate3d(575px, 0px, 0px); } }

